/**
 * Establish grid css grid-template-columns based upon attributes associated with columns
 * 
 * @param {*} isNewBlock 
 * @param {*} tableIsResolving 
 * @param {*} columns 
 * @returns 
 */
export function processColumns(isNewBlock, tableIsResolving, enableFutureFeatures, columns) {
    if (isNewBlock || tableIsResolving) {
        return undefined
    }

    let newGridColumnStyle = ''
    {
        columns.map(({ column_id, column_name, attributes, classes }) => {
            console.log('Column ID - ' + newGridColumnStyle)
            console.log(attributes)
            const { columnWidthType,
                minWidth,
                minWidthUnits,
                maxWidth,
                maxWidthUnits,
                fixedWidth,
                fixedWidthUnits,
                disableForTablet,
                disableForPhone
            } = attributes;

            let sizing = '';

            if (column_id === '1' && enableFutureFeatures) {
                newGridColumnStyle = newGridColumnStyle + '40px '
            }
            switch (columnWidthType) {
                case 'Proportional':
                    {
                        if (minWidth > 0) {
                            sizing = 'minmax(' + minWidth + minWidthUnits + ', ' + maxWidth + 'fr) '
                        } else (
                            // sizing = '1fr '
                            sizing = maxWidth + 'fr '
                        )
                        newGridColumnStyle = newGridColumnStyle + sizing;
                        break;
                    }
                case 'Auto':
                    {
                        newGridColumnStyle = newGridColumnStyle + 'auto ';
                        break;
                    }
                case 'Fixed':
                    {
                        newGridColumnStyle = newGridColumnStyle + fixedWidth + fixedWidthUnits + ' ';
                        break;
                    }
                case 'Custom':
                    {
                        sizing = 'minmax(' + minWidth + minWidthUnits + ', ' + maxWidth + maxWidthUnits + ') '
                        newGridColumnStyle = newGridColumnStyle + sizing;
                        break;
                    }
                default:
                    console.log('Unrecognized Attibute Type')
            }

            // if (column_id === '0') {
            // 	newGridColumnStyle = newGridColumnStyle + '20px ';
            // } else {
            // 	newGridColumnStyle = newGridColumnStyle + 'auto ';
            // }
        })
    }
    console.log('grid-template-columns = ' + newGridColumnStyle)
    // setTableStale(false)
    return newGridColumnStyle
}

/**
 * Establish grid css grid-template-rowss based upon attributes associated with rows
 * 
 * @param {*} isNewBlock 
 * @param {*} tableIsResolving 
 * @param {*} rows 
 * @returns 
 */
export function processHeaderRow(isNewBlock, tableIsResolving, rows) {
    if (isNewBlock || tableIsResolving) {
        return undefined
    }

    let newGridRowStyle = ''
    {
        rows.map(({ row_id, attributes, classes }) => {
            console.log('Row ID - ' + newGridRowStyle)
            console.log(attributes)
            const { rowHeightType,
                minHeight,
                minHeightUnits,
                maxHeight,
                maxHeightUnits,
                fixedHeight,
                fixedHeightUnits,
                isHeader
            } = attributes;

            let sizing = '';

            if (isHeader) {
                switch (rowHeightType) {
                    case 'Auto':
                        {
                            newGridRowStyle = newGridRowStyle + 'auto ';
                            break;
                        }
                    case 'Fixed':
                        {
                            newGridRowStyle = newGridRowStyle + fixedHeight + fixedHeightUnits + ' ';
                            break;
                        }
                    case 'Custom':
                        {
                            sizing = 'minmax(' + minHeight + minHeightUnits + ', ' + maxHeight + maxHeightUnits + ') '
                            newGridRowStyle = newGridRowStyle + sizing;
                            break;
                        }
                    default:
                        console.log('Unrecognized Attibute Type')
                }
            }

            // if (row_id === '0') {
            //     newGridRowStyle = newGridRowStyle + '25px ';
            // }  else {
            //     newGridRowStyle = newGridRowStyle + 'auto ';
            // }
        })
    }
    // setTableStale(false)
    return newGridRowStyle
}

/**
 * Establish grid css grid-template-rowss based upon attributes associated with rows
 * 
 * @param {*} isNewBlock 
 * @param {*} tableIsResolving 
 * @param {*} rows 
 * @returns 
 */
export function processBodyRows(isNewBlock, tableIsResolving, rows) {
    if (isNewBlock || tableIsResolving) {
        return undefined
    }

    let newGridRowStyle = ''
    {
        rows.map(({ row_id, attributes, classes }) => {
            console.log('Row ID - ' + newGridRowStyle)
            console.log(attributes)
            const { rowHeightType,
                minHeight,
                minHeightUnits,
                maxHeight,
                maxHeightUnits,
                fixedHeight,
                fixedHeightUnits,
                isHeader
            } = attributes;

            let sizing = '';

            if (!isHeader) {
                switch (rowHeightType) {
                    case 'Auto':
                        {
                            newGridRowStyle = newGridRowStyle + 'auto ';
                            break;
                        }
                    case 'Fixed':
                        {
                            newGridRowStyle = newGridRowStyle + fixedHeight + fixedHeightUnits + ' ';
                            break;
                        }
                    case 'Custom':
                        {
                            sizing = 'minmax(' + minHeight + minHeightUnits + ', ' + maxHeight + maxHeightUnits + ') '
                            newGridRowStyle = newGridRowStyle + sizing;
                            break;
                        }
                    default:
                        console.log('Unrecognized Attibute Type')
                }
            }
        })
    }
    // {
    //     rows.filter(row => !row.attributes.isHeader && row.row_id !== '0')
    //         .map(({ row_id, attributes, classes }) => {
    //             console.log('Row ID - ' + newGridRowStyle)
    //             newGridRowStyle = newGridRowStyle + 'auto ';
    //         })
    // }
    // setTableStale(false)
    return newGridRowStyle
}

/**
 * Create Styling Variable for showing inner grid borders/lines
  * 
  * @param {*} isNewBlock 
  * @param {*} tableIsResolving 
  * @param {*} showGridLines 
  * @returns 
  */
export function gridBandedRowTextColorStyle(isNewBlock, tableIsResolving, color) {
    if (isNewBlock || tableIsResolving) {
        return undefined;
    };
    return color;
}

export function gridBandedRowBackgroundColorStyle(isNewBlock, tableIsResolving, color) {
    if (isNewBlock || tableIsResolving) {
        return undefined;
    };
    return color;
}

/**
* Create Styling Variable for showing inner grid borders/lines
* 
* @param {*} isNewBlock 
* @param {*} tableIsResolving 
* @param {*} showGridLines 
* @returns 
*/
export function getGridHeaderBackgroundColorStyle(isNewBlock, tableIsResolving, tableColor, blockColor) {
    if (isNewBlock || tableIsResolving) {
        return undefined;
    };

    if (tableColor) {
        return tableColor;
    };

    return blockColor;
}


/**
 * Create Styling Variable for showing inner grid borders/lines
  * 
  * @param {*} isNewBlock 
  * @param {*} tableIsResolving 
  * @param {*} showGridLines 
  * @returns 
  */
export function gridInnerBorderStyle(isNewBlock, tableIsResolving, showGridLines) {
    if (isNewBlock || tableIsResolving) {
        return undefined;
    };
    console.log('show grid lines = ' + showGridLines)
    if (showGridLines) {
        return 'solid';
    };

    return 'hidden';
}

/**
* Create Styling Variable for inner grid borders/lines width
* 
* @param {*} isNewBlock 
* @param {*} tableIsResolving 
* @param {*} showGridLines 
* @returns 
*/
export function gridInnerBorderWidthStyle(isNewBlock, tableIsResolving, showGridLines, gridLineWidth) {
    if (isNewBlock || tableIsResolving) {
        return undefined;
    };

    if (!showGridLines) {
        return '0px';
    };

    return String(gridLineWidth) + 'px';
}

export function startGridRowNbr(enableHeader, showBorders) {
    let startGridLine = 1
    startGridLine = enableHeader ? startGridLine + 1 : startGridLine
    startGridLine = showBorders ? startGridLine + 1 : startGridLine

    return startGridLine;
}
// endGridRowNbr(1, 'Header', numRows, enableHeaderRow, false)

export function endGridRowNbr(startGridLine, rowGroup, numRows, enableHeader, showBorders, enableFooter) {
    let endGridLine

    switch (rowGroup) {
        case 'Header':
            {
                endGridLine = 2
                endGridLine = showBorders ? endGridLine++ : endGridLine
                break;
            }
        case 'Body':
            {
                endGridLine = startGridLine + numRows
                endGridLine = showBorders ? endGridLine++ : endGridLine
                endGridLine = enableHeader ? endGridLine - 1 : endGridLine
                endGridLine = enableFooter ? endGridLine - 1 : endGridLine
                break;
            }
        default:
            console.log('Unknown row type')
    }

    return endGridLine;
}

export function getHeaderTextAlignmentStyle(isNewBlock, tableIsResolving, textAlignment) {
    if (isNewBlock || tableIsResolving) {
        return undefined;
    };

    return textAlignment;
}



/**
 * The BorderBoxControl stores the syle values as a flat object (simple) or as nested objects
 * (complex).  We evaluate the object value to determine which type it is.
 * 
 * @param {*} border 
 * @returns 
 */
export function getBorderStyleType(border) {
    if (border) {
        const borderWrapper = Object.entries(border);
        for (var i = 0; i < borderWrapper.length; i++) {
            if (borderWrapper[i].some(value => { return typeof value == "object" })) {
                console.log(borderWrapper[i]);
                return 'split'
            }
        }
        return 'flat';
    };
    return 'unknown';
}

/**
 * Get Style value for the specified border segment and attribute
 * 
 * @param {*} border 
 * @param {*} borderLocation 
 * @param {*} borderAttribute 
 * @param {*} borderType 
 * @returns 
 */
export function getBorderStyle(border, borderLocation, borderAttribute, borderType) {
    switch (borderType) {
        case 'split':
            {
                return border[borderLocation][borderAttribute];
            }

        case 'flat':
            {
                return border[borderAttribute];
            }

        default:
            {
                switch (borderAttribute) {
                    case 'color':
                        {
                            return 'black';
                        }

                    case 'style':
                        {
                            return 'solid';
                        }

                    case 'width':
                        {
                            return '1px';
                        }

                }
            }
    }
    // if (borderType === 'split') {
    //     return border[borderLocation][borderAttribute]
    // }

    // if (borderType === 'flat') {
    //     return border[borderAttribute]
    // }

    // return 'unknown'
}
